.index-page {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	background-color: #f9f9f9;
	font-family: 'Arial', sans-serif;
  }
  
  .header {
	text-align: center;
	background-color: #4a90e2;
	color: white;
	padding: 20px 0;
	font-size: 2.5rem;
  }
  
  .main-content {
	display: flex;
	flex-grow: 1;
	padding: 20px;
	justify-content: space-between;
  }
  
  .sidebar {
	width: 250px;
	padding: 20px;
	background-color: #333;
	color: white;
  }
  
  .sidebar ul {
	list-style: none;
	padding: 0;
  }
  
  .sidebar ul li {
	margin: 10px 0;
  }
  
  .sidebar ul li a {
	color: white;
	text-decoration: none;
  }
  
  .content {
	flex-grow: 1;
	padding: 20px;
	background-color: #fff;
	margin-left: 20px;
	border-radius: 8px;
  }
  
  .section-with-image {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 40px;
  }
  
  .image-container {
	flex: 1;
	margin-right: 20px;
  }
  
  .image-container img {
	width: 100%;
	height: auto;
	border-radius: 8px;
  }
  
  .text {
	flex: 1;
  }
  
  .team-section {
	margin-bottom: 20px;
  }
  
  .footer {
	background-color: #4a90e2;
	color: white;
	text-align: center;
	padding: 10px 0;
  }
  